export default {
  name: "list-form",
  data() {
    return {
      title: "",
      color: "#9932CC",
    };
  },
  computed: {},
  methods: {
    submitList() {
      this.$store.dispatch("createNewList", {
        title: this.title,
        color: this.color,
      });
      this.title = "";
    },
  },
  components: {},
};
