import Task from "./Task";
export default {
  name: "List",
  mounted() {
    this.$store.dispatch("getTasksByListId", this.list.id);
  },
  data() {
    return {
      title: "",
    };
  },
  props: ["list"],
  computed: {
    tasks() {
      return this.$store.state.TaskStore.tasks[this.list.id];
    },
    tasksCompleted() {
      if (this.tasks) {
        return this.tasks.filter((e) => e.complete == true).length > 0;
      } else {
        return false;
      }
    },
  },
  methods: {
    submitTask() {},
    deleteList() {
      this.$store.dispatch("deleteList", this.list.id);
    },
    createNewTask() {
      this.$store.dispatch("createNewTask", {
        title: this.title,
        listId: this.list.id,
      });
    },
    removeCompleted() {
      for (let i = 0; i < this.tasks.length; i++) {
        let task = this.tasks[i];
        if (task.complete) {
          this.$store.dispatch("deleteTask", task);
        }
      }
    },
  },
  components: {
    Task,
  },
};
