export default {
  name: "task",
  data() {
    return {};
  },
  props: ["task"],
  computed: {},
  methods: {
    updateTask() {
      this.task.complete = !this.task.complete;
      this.$store.dispatch("updateTask", this.task);
    },
    deleteTask() {
      this.$store.dispatch("deleteTask", this.task);
    },
  },
  components: {},
};
